/**
 * See "Angular 2, decorators and class inheritance"
 * https://medium.com/@ttemplier/angular2-decorators-and-class-inheritance-905921dbd1b7
 * 
 */
const METADATA = ['design:paramtypes', 'propMetadata', 'parameters'];

/**
 * @hidden
 */
export function InheritMetadata(): Function {
  return (target: Function) => {
    const parentTarget = Object.getPrototypeOf(target.prototype).constructor;
    METADATA.forEach(key => {
      const parentMeta = Reflect.getMetadata(key, parentTarget);
      if (parentMeta) {
        const ownMeta = Reflect.getMetadata(key, target);
        if (ownMeta) {
          Object.assign(ownMeta, parentMeta);
        } else {
          Reflect.defineMetadata(key, parentMeta, target);
        }
      }
    });
  };
};



